.hero {
  padding-top: var(--gap-primary);
  &__title {
    @include adaptiveValue(font-size, 120, 30, 1920, 360, 1);
    text-transform: uppercase;
    font-weight: 500;
    @include adaptiveValue(margin-bottom, 30, 15, 1920, 360, 1);
    color: var(--primary);
    word-break: break-word;

    span {
      @include adaptiveValue(padding-left, 100, 42, 1920, 576, 1);

      @include _576 {
        padding-left: 0;
      }
    }
  }

  &__img {
    @include adaptiveValue(margin-bottom, 40, 20, 1920, 360, 1);
    min-height: 170px;
  }

  &__warranty {
    display: flex;
    justify-content: center;
    padding: rem(10) 0;
    background-color: var(--primary);
    color: var(--white);
    &-title {
      @include adaptiveValue(font-size, 40, 18, 1920, 360, 1);
      font-weight: 500;
      max-width: rem(800);
      text-align: center;
    }
  }
}
